:root {
  /*- Dark Blue (Dark Mode Elements): hsl(209, 23%, 22%)
  - Very Dark Blue (Dark Mode Background): hsl(207, 26%, 17%)
  - Very Dark Blue (Light Mode Text): hsl(200, 15%, 8%)
  - Dark Gray (Light Mode Input): hsl(0, 0%, 52%)
  - Very Light Gray (Light Mode Background): hsl(0, 0%, 98%)
  - White (Dark Mode Text & Light Mode Elements): hsl(0, 0%, 100%)*/
  --color-dark-el: hsl(209, 23%, 22%);
  --color-dark-bg: hsl(207, 26%, 17%);
  --color-light-text: hsl(200, 15%, 8%);
  --color-light-input: hsl(0, 0%, 52%);
  --color-light-bg: hsl(0, 0%, 98%);
  --color-dark-light-text-bg: hsl(0, 0%, 100%);
}
.dark-mode {
  --font-color: var(--color-dark-light-text-bg); 
  --el-color: var(--color-dark-el);
  --bg-color: var(--color-dark-bg);
  --font-color-input: var(--color-dark-light-text-bg);
  --arrow-select-color: white;
  --search-svg: url(/static/media/search-dark.cbcc2731.svg);
  --mode-svg: url(/static/media/moon-solid.8a4aa51c.svg);
}
.light-mode {
  --font-color: var(--color-light-text); 
  --el-color: var(--color-dark-light-text-bg);
  --bg-color: var(--color-light-bg);
  --font-color-input: var(--color-light-input);
  --arrow-select-color: black;
  --search-svg: url(/static/media/search-light.a49c1def.svg);
  --mode-svg: url(/static/media/moon-regular.5ed8bf72.svg);
}
.semi-bold {
  font-weight: 600;
}
.App {
  font-family: 'Nunito Sans', sans-serif;
  color: var(--font-color);
  background-color: var(--bg-color);
  min-height: 100vh;
  /*transition: all 0.3s;*/
}
/* error api */
.error {
  text-align: center;
  font-weight: bold;
  font-size: 18px;
}

header {
    box-shadow: 0px 2px 4px rgba(0,0,0,0.05);
    background-color: var(--el-color);
    transition: background-color 0.3s;
}
header > div {
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1280px;
    height: 80px;
}
#select-mode {
    cursor: pointer;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    display: flex;
    font-weight: 600;
}
#select-mode img {
    content: var(--mode-svg);
    margin-right: 6px;
}
@media (max-width: 1350px) {
    header > div {
        padding: 0 6vw;
    }
}
h1 {
    font-size:24px;
    line-height: 33px;
    font-weight: bold;     
}
@media (max-width: 450px) {
    header > div {
        padding: 0 16px;
    }
    h1 {
        font-size:14px;
        line-height: 20px;  
    } 
    #select-mode {
        font-size: 12px;
    }
    #select-mode img {
        width: 16px;
        height: 16px;
    }
}
/* search filter panel */

#search-filter-panel {
    margin: 0 auto;
    display:flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1280px;
    padding: 48px 0px;
  }
  
  #search-country-wrapper input, #select-region-btn {
    height: 56px;
    font-size: 14px;
    box-shadow: 0px 2px 9px rgba(0,0,0,0.05);
    font-weight: 400;
    line-height: 20px;
    transition: all 0.3s;
    background-color: var(--el-color);
    border-radius: 5px;
  }
  
  #search-country-wrapper input {
    width: 480px;
    padding-left:74px;
    background-repeat: no-repeat;
    background-position: 32px;
    background-image: var(--search-svg);
    font-family: "Nunito Sans";
    border: none;
    outline: none;
    color: var(--font-color-input);
  }
  #search-country-wrapper input::-webkit-input-placeholder {
    color: var(--font-color-input);
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
  }
  #search-country-wrapper input:-ms-input-placeholder {
    color: var(--font-color-input);
    -ms-transition: all 0.3s;
    transition: all 0.3s;
  }
  #search-country-wrapper input::-ms-input-placeholder {
    color: var(--font-color-input);
    -ms-transition: all 0.3s;
    transition: all 0.3s;
  }
  #search-country-wrapper input::placeholder {
    color: var(--font-color-input);
    transition: all 0.3s;
  }
  
  #select-region-wrapper {
    position: relative;
  }
  #select-region-btn {
    display: flex;
    cursor: pointer;
    justify-content: space-between;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    align-items: center;
    width: 200px;
    padding: 19px 17px 17px 24px;
  }
  #select-region-wrapper svg {
    fill: var(--arrow-select-color);
    transition: all 0.3s;
  }
  #select-region-nav {
    position: absolute;
    display: flex;
    align-items: center;
    background: var(--el-color);
    top: 60px;
    height: 192px;
    width: 100%;
    border-radius: 5px;
    font-size: 14px;
    font-weight: normal;
    line-height: 20px;
    padding-left: 24px;
    transition: all 0.3s;
    overflow: hidden;
  }
  #select-region-nav li {
    cursor: pointer;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
  }
  #select-region-nav li:hover {
   font-weight: 600;
  }
  #select-region-nav li:not(:last-child) {
    margin-bottom: 8px;
  }
  .slide-enter-from,
  .slide-leave-to {
    max-height: 0;
  }
  .slide-enter-to,
  .slide-leave-from {
    max-height: 192px;
  }
  .slide-enter-active,
  .slide-leave-active {
    transition: max-height 0.3s;
    transform-origin: bottom;
  }
  
  /* section Countries */
  
  #countries {
    margin: 0 auto;
    max-width: -webkit-max-content;
    max-width: -moz-max-content;
    max-width: max-content;
    padding-bottom: 48px;
  }
  #countries-box {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 75px;
    gap: 75px;
    max-width: 1280px;
  }
  
  /* media queries */
  
  @media (max-width: 1350px) {
    #countries-box {
      grid-template-columns: 1fr 1fr 1fr;
    }
    #search-filter-panel {
      padding: 48px 6vw;
    }  
  }
  
  @media (max-width: 1085px) {
    #countries-box {
      grid-template-columns: 1fr 1fr;
    }  
  }
  
  @media (max-width: 850px) {
    #search-filter-panel {
      flex-direction: column;
      align-items: start;
    }
    #select-region-btn {
      margin-top: 40px;
    }
    #search-country-wrapper {
      width: 100%;
    }
    #search-country-wrapper input {
      width: 100%;
    }
    #select-region-nav {
      top: 100px;
    }
  }
  
  @media (max-width: 680px) {
    #countries-box {
      grid-template-columns: 1fr;
    }  
  }
  
  @media (max-width: 450px) {
    #countries-box {
      grid-gap: 40px;
      gap: 40px;
    }
    #search-filter-panel {
      padding: 24px 16px 32px 16px;
    }
    #search-country-wrapper input, #select-region-btn {
      font-size: 12px !important;
      line-height: 20px !important;
      height: 48px !important;
    }
    #select-region-nav {
      font-size: 12px;
      line-height: 16px;
      top: 92px;
      height: 168px;
    }
  }
.country-box {
    display: flex;
    flex-wrap: wrap;
    width: 264px;
    height: 336px;
    background-color:var(--el-color);
    box-shadow: 0px 0px 7px 2px rgba(0,0,0,0.03);
    border-radius: 5px;
    overflow: hidden;
    transition: all 0.3s;
    color: inherit;
    text-decoration: none;
    cursor: pointer;
  }
  .country-box img {
    width: 100%;
    height: 160px;
    object-fit: cover;
  }
  .country-box-info {
    width: 100%;
    height: 176px;
    padding: 24px 24px 46px 24px;
  }
  .country-box-info h2 {
    font-size: 18px;
    line-height: 26px;
    font-weight: bold;
    margin-bottom: 16px;
  }
  .country-box-info p {
    font-size: 14px;
    line-height: 16px;
  }
  .country-box-info p:not(:last-child) {
    margin-bottom: 8px;
  }
